*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }
  @media only screen and (max-width: 75em) {
    html {
      font-size: 50%; } }

body {
  font-weight: 300;
  line-height: 1.6;
  background-color: whitesmoke;
  display: flex;
  flex-direction: column;
  max-width: 384em;
  min-width: 36rem;
  margin: 0 auto;
  flex-wrap: wrap;
  line-height: 1.6;
  font-family: "Nunito", sans-serif; }

::selection {
  background-color: #fff;
  color: black; }

mark {
  background-color: #fff;
  color: black;
  opacity: 0.8; }
