.about {
  display: flex;
  flex-direction: column;
  z-index: 2;
  color: #fff;
  font-size: 2.2rem;
  align-items: flex-start;
  line-height: 3.5rem; }
  .about__header {
    flex: 100%;
    animation: moveRight 1s ease-out;
    animation-delay: 0.1s;
    font-size: 4rem;
    font-weight: 400;
    margin: 0 2rem; }
  .about__wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 3.5rem 2rem 2rem 2rem;
    justify-content: center; }
    @media only screen and (max-width: 56.52em) {
      .about__wrapper {
        flex-direction: column; } }
  .about__left {
    flex: 60%;
    display: flex;
    flex-flow: column;
    align-items: flex-start; }
    .about__left--first {
      animation: moveRight 1s ease-out;
      animation-delay: 0.2s;
      animation-fill-mode: backwards;
      font-size: 3.5rem;
      font-weight: 400;
      margin: 0 2rem; }
    .about__left :nth-child(1) {
      animation: moveRight 1.1s ease-out;
      animation-delay: 0.3s;
      animation-fill-mode: backwards; }
    .about__left :nth-child(2) {
      animation: moveRight 1.2s ease-out;
      animation-delay: 0.4s;
      animation-fill-mode: backwards; }
    .about__left :nth-child(3) {
      animation: moveRight 1.3s ease-out;
      animation-delay: 0.5s;
      animation-fill-mode: backwards; }
    .about__left :nth-child(4) {
      animation: moveRight 1.4s ease-out;
      animation-delay: 0.6s;
      animation-fill-mode: backwards; }
    .about__left :nth-child(5) {
      animation: moveRight 1.5s ease-out;
      animation-delay: 0.7s;
      animation-fill-mode: backwards; }
  .about__right {
    flex: 40%;
    display: flex;
    flex-flow: column;
    align-items: flex-end;
    justify-content: center;
    animation: moveLeft 1.3s ease-out;
    animation-delay: 0.8s;
    animation-fill-mode: backwards; }
    @media only screen and (max-width: 56.52em) {
      .about__right {
        margin-top: 10rem;
        margin-bottom: 5rem;
        align-items: center; } }
  .about__text {
    margin: 0.9rem 0rem;
    line-height: 2.1rem; }
  .about__link {
    text-decoration: none; }

.scene {
  display: inline-block;
  width: 20rem;
  height: 20rem;
  perspective: 600px; }

.cube {
  position: relative;
  width: inherit;
  height: inherit;
  transform-style: preserve-3d;
  animation: spin 15s linear infinite; }

.cube-face {
  width: inherit;
  height: inherit;
  position: absolute;
  background: #00000062;
  background: #999fa1cc; }

.cube-face-front {
  transform: translate3d(0, 0, 10rem); }

.cube-face-back {
  transform: rotateY(180deg) translate3d(0, 0, 10rem); }

.cube-face-left {
  transform: rotateY(-90deg) translate3d(0, 0, 10rem); }

.cube-face-right {
  transform: rotateY(90deg) translate3d(0, 0, 10rem); }

.cube-face-top {
  transform: rotateX(90deg) translate3d(0, 0, 10rem); }

.cube-face-bottom {
  transform: rotateX(-90deg) translate3d(0, 0, 10rem); }

@keyframes spin {
  0% {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg); }
  100% {
    transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg); } }
